import {
  Card,
  Box,
  Typography,
  Avatar,
  Grid,
  alpha,
  styled
} from '@mui/material';

import { useEffect } from 'react';
import http from 'src/http';
import { useState } from 'react';
import {GoPerson} from 'react-icons/go';
import { GiFlatPlatform } from 'react-icons/gi';
import {MdAttachMoney} from 'react-icons/md'
import OrdersTable from 'src/content/applications/Orders/OrdersTable';
import { IOrder } from 'src/models/product';
import { UserContext } from 'src/context/UserContext';
import React from 'react';

const AvatarWrapper = styled(Avatar)(
  ({ theme }) => `
    margin: ${theme.spacing(0, 0, 1, -0.5)};
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: ${theme.spacing(1)};
    padding: ${theme.spacing(0.5)};
    border-radius: 60px;
    height: ${theme.spacing(5.5)};
    width: ${theme.spacing(5.5)};
    background: ${
      theme.palette.mode === 'dark'
        ? theme.colors.alpha.trueWhite[30]
        : alpha(theme.colors.alpha.black[100], 0.07)
    };
  
    img {
      background: ${theme.colors.alpha.trueWhite[100]};
      padding: ${theme.spacing(0.5)};
      display: block;
      border-radius: inherit;
      height: ${theme.spacing(4.5)};
      width: ${theme.spacing(4.5)};
    }
`
);

function WatchListColumn() {
 

  const [users, setUsers] = useState('');
  const [totalProducts, setTotalProducts] = useState(null);
  const [mostRentedProduct, setMostRentendProduct] = useState<any>(null);
  const [recentOrders, setRecentOrders] = useState<IOrder[]>([]);

  const {
    usuario
  } = React.useContext(UserContext);

  async function getAllClients(){
    const { data } = await http.get("/usuarios/total");
    setUsers(data.total);
  }

  async function getAllOrders(){
    const { data } = await http.get("/pedidos/total");
    setTotalProducts(data.pedidos);
    setMostRentendProduct(data.produto);
  }

  async function getRecentOrders(){
    const { data } = await http.get(`/pedidos/painel/${usuario?.id}`);
    setRecentOrders(data);
  }


  useEffect(()=>{
    getAllClients();
    getAllOrders();
    getRecentOrders();
  }, []);

  

  return (
    <>
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="stretch"
        spacing={3}
      >
        <Grid item md={4} xs={12}>
          <Card
            sx={{
              overflow: 'visible'
            }}
          >
            <Box
              sx={{
                p: 3
              }}
            >
              <Box display="flex" alignItems="center">
                <AvatarWrapper>
                  <GoPerson style={{width: '30px', height: '30px', fill: '#223354'}}/>
                </AvatarWrapper>
                <Box>
                  <Typography variant="h4" noWrap>
                    Clientes
                  </Typography>
                </Box>
              </Box>
              <Box
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'flex-start',
                  pt: 3
                }}
              >
                <Typography
                  variant="h2"
                  sx={{
                    pr: 1,
                    mb: 1
                  }}
                >
                  {users} Cadastrados
                </Typography>

              </Box>
              <Box
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'flex-start'
                }}
              >
                <Typography
                  variant="body2"
                  color="text.secondary"
                  sx={{
                    pl: 1
                  }}
                >
                </Typography>
              </Box>
            </Box>
          </Card>
        </Grid>
        <Grid item md={4} xs={12}>
          <Card
            sx={{
              overflow: 'visible'
            }}
          >
            <Box
              sx={{
                p: 3
              }}
            >
              <Box display="flex" alignItems="center">
                <AvatarWrapper>
                <MdAttachMoney style={{width: '30px', height: '30px', fill: '#223354'}}/>
                </AvatarWrapper>
                <Box>
                  <Typography variant="h4" noWrap>
                  Valor Total
                  </Typography>
                  <Typography variant="subtitle1" noWrap>
                  
                  </Typography>
                </Box>
              </Box>
              <Box
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'flex-start',
                  pt: 3
                }}
              >
                <Typography
                  variant="h2"
                  sx={{
                    pr: 1,
                    mb: 1
                  }}
                >
                  {totalProducts?.valor_total?.toLocaleString('pt-br',{style: 'currency', currency: 'BRL'})}
                </Typography>
              </Box>
              <Box
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'flex-start'
                }}
              >
                <Typography
                  variant="body2"
                  color="text.secondary"
                  sx={{
                    pl: 1
                  }}
                >
        
                </Typography>
              </Box>
            </Box>
          </Card>
        </Grid>
        <Grid item md={4} xs={12}>
          <Card
            sx={{
              overflow: 'visible'
            }}
          >
            <Box
              sx={{
                p: 3
              }}
            >
              <Box display="flex" alignItems="center">
                <AvatarWrapper>
                <GiFlatPlatform style={{width: '30px', height: '30px', fill: '#223354'}}/>
                </AvatarWrapper>
                <Box>
                  <Typography variant="h4" noWrap>
                  Mais Alugado
                  </Typography>
                  <Typography variant="subtitle1" noWrap>
                
                  </Typography>
                </Box>
              </Box>
              <Box
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'flex-start',
                  pt: 3
                }}
              >
                <Typography
                  variant="h2"
                  sx={{
                    pr: 1,
                    mb: 1
                  }}
                >
                  {mostRentedProduct?.nome}
                </Typography>

              </Box>
              <Box
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'flex-start'
                }}
              >
                <Typography
                  variant="body2"
                  color="text.secondary"
                  sx={{
                    pl: 1
                  }}
                >
                </Typography>
              </Box>
            </Box>
          </Card>
        </Grid>
      </Grid>
      <div style={{marginTop: '3rem'}}>
        {recentOrders &&
          <OrdersTable orders={recentOrders} panel={true} className=""/>
        }
      </div>
    </>
  );
}

export default WatchListColumn;
