import { FC, ChangeEvent, useState, useContext, useCallback} from 'react';
import { api } from 'src/api/api';
import numeral from 'numeral';
import PropTypes from 'prop-types';
import {useEffect} from 'react';
import { FaCloudDownloadAlt } from 'react-icons/fa';
import {
  Tooltip,
  Divider,
  Box,
  FormControl,
  InputLabel,
  Card,
  Checkbox,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Select,
  MenuItem,
  Typography,
  useTheme,
  CardHeader
} from '@mui/material';

import { read, utils, writeFileXLSX } from 'xlsx';
import { IOrder } from 'src/models/product';
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';
import { RegisterOrderModal } from 'src/components/Modals/RegisterOrderModal/RegisterOrderModal';
import moment from 'moment';
import BulkActions from './BulkActions';
import { BsEye } from 'react-icons/bs'
import { UserContext } from 'src/context/UserContext';
import http from 'src/http';

interface OrdersTableProps {
  className?: string;
  orders: IOrder[];
  panel: boolean;
}


const OrdersTable: FC<OrdersTableProps> = ({ orders, panel }) => {

  const [selectedOrders, setSelectedOrders] = useState<string[]>([]);
  const selectedBulkActions = selectedOrders.length > 0;
  const [page, setPage] = useState<number>(0);
  const [limit, setLimit] = useState<number>(5);

const applyFilters = (
  orders: IOrder[],
): IOrder[] => {
  return orders.filter((order) => {
    let matches = true;

    /*if (filters.status && cryptoOrder.status !== filters.status) {
      matches = false;
    }*/

    return matches;
  });
};

const applyPagination = (
  orders: IOrder[],
  page: number,
  limit: number
): IOrder[] => {
  return orders.slice(page * limit, page * limit + limit);
};


const exportFile = useCallback(() => {
  const ws = utils.json_to_sheet(orders);
  const wb = utils.book_new();
  utils.book_append_sheet(wb, ws, "Data");
  writeFileXLSX(wb, "Pedidos.xlsx");
}, [orders]);

  const handleSelectAllOrders = (
    event: ChangeEvent<HTMLInputElement>
  ): void => {
    setSelectedOrders(
      event.target.checked
        ? orders.map((order) => order.id)
        : []
    );
  };

  const handleSelectOneCryptoOrder = (
    event: ChangeEvent<HTMLInputElement>,
    order: string
  ): void => {
    if (!selectedOrders.includes(order)) {
      setSelectedOrders((prevSelected) => [
        ...prevSelected,
        order
      ]);
    } else {
      setSelectedOrders((prevSelected) =>
        prevSelected.filter((id) => id !== order)
      );
    }
  };

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage);
  };

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value));
  };

  const filteredOrders = applyFilters(orders);
  const paginatedOrders = applyPagination(
    filteredOrders,
    page,
    limit
  );
  const selectedSomeOrders =
    selectedOrders.length > 0 &&
    selectedOrders.length < orders.length;

  const selectedAllOrders =
  selectedOrders.length === orders.length;
  const theme = useTheme();


  const [data, setData] = useState({});
  const [idOrder, setIdOrder] = useState<IOrder[]>([]);
  const [modal, setModal] = useState<Boolean>(false)
  const [idTokenContrato, setIdTokenContrato] = useState('')
  

  async function queryOrdersById(idOrder: any){
    setModal(!modal)
    setData(null)
    const {data} = await api.get(`/pedidos/${idOrder}`);
    if(data){
      setData({data});
    }else{
      setData(null);
    }
  }
  //Retorna status do documento do clicksign.
  async function getStatusDocument(idTokenContrato: string){
      http.get(`/clicksign/get-status-document/${idTokenContrato}`, {
        method: 'GET',
        headers:{
          'Content-Type': 'application/json'
        },
      })
      .then(response=>{
        return response.data.data.document.status
      }).catch(err=>{
        console.log(err)
      })
    
  }

  useEffect(()=>{
    getStatusDocument(idTokenContrato)
  }, [idTokenContrato])



  return (
    <Card >
      {selectedBulkActions && (
        <Box flex={1} p={2}>
          <BulkActions />
        </Box>
      )}
      {!selectedBulkActions && (
        <CardHeader
          action={
            <Box width={150}>
              <FormControl fullWidth variant="outlined">
              </FormControl>
              
                <FaCloudDownloadAlt onClick={exportFile} style={{
                textAlign: 'right',
                position: 'relative',
                left: '7rem',
                height: '25px',
                width: '25px',
                cursor: 'pointer'
               }}/>
             
            </Box>
            
            
          }
          title={panel? "Pedidos recentes": "Pedidos cadastrados"}
          
        />
      )}
      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
            <TableCell></TableCell>
              <TableCell>Empresa</TableCell>
              <TableCell>Contato</TableCell>
              <TableCell>Início e Devolução</TableCell>
              <TableCell align="right">Valor</TableCell>
              <TableCell align="right">Status</TableCell>
              <TableCell align="right">Ações</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedOrders.map((order) => {
              const isOrderSelected = selectedOrders.includes(
                order.id
              );
              return (
                <TableRow
                  hover
                  key={order.id}
                  selected={isOrderSelected}
                >
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isOrderSelected}
                      onChange={(event: ChangeEvent<HTMLInputElement>) =>
                        handleSelectOneCryptoOrder(event, order.id)
                      }
                      value={isOrderSelected}
                    />
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {order?.cliente?.razao_social}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {order?.cliente?.email}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {moment(order.data_inicio).utc().format('DD/MM/yyyy')}
                    </Typography>
                    <Typography 
                        variant="body1"
                        fontWeight="bold"
                        color="text.secondary"
                        gutterBottom 
                        noWrap
                    >
                      {moment(order.data_entrega).utc().format('DD/MM/yyyy')}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >

                    
                    </Typography>
                    <Typography variant="body2" color="text.secondary" noWrap>
                    R${numeral(order.vr_total).format(
                        `0,0.00`
                      )}
                    </Typography>
                  </TableCell>

                  <TableCell align="right">
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >

                    
                    </Typography>
                    <Typography variant="body2" color="text.secondary" noWrap>
                    {order?.status === 'encerrado' && <p style={{color: 'red', fontSize: '.875rem', fontWeight: 'bold'}}>Encerrado</p>
                    }
                      {order?.status === 'aberto' && <p style={{color: 'green', fontSize: '.875rem', fontWeight: 'bold'}}>Em aberto</p>
                    }
                    </Typography>
                  </TableCell>


                    <TableCell align="right">
                    <Tooltip title="Visualizar pedido" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>queryOrdersById(order?.id)}
                      >
                        <BsEye style={{width: '20px', height: '20px'}}/>
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={filteredOrders.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
        />
      </Box>
      {data  && modal &&(
        <RegisterOrderModal 
        openModal={modal}
        setModal={setModal}
        data={data}
        edit={true}
      />
      )}
    </Card>
  );
};

OrdersTable.propTypes = {
  orders: PropTypes.array.isRequired
};

OrdersTable.defaultProps = {
  orders: []
};

export default OrdersTable;
